<template>
  <div class="loading" v-if="this.$store.state.loading == true">
    <div class="spinner-grow" style="width: 3rem; height: 3rem" role="status">
      <span class="visually-hidden">Loading...</span>
    </div>
  </div>
  <div v-if="!this.$store.state.loading == true">
    <nav
      class="main-breadcrumb"
      aria-label="breadcrumb"
      :style="{
        'background-image':
          'url(' + require('@/assets/images/backgrounds/broad.jpg') + ')',
      }"
    >
      <div class="container">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link :to="{ name: 'Home' }"> الرئيسية</router-link>
          </li>
          <li class="breadcrumb-item active" aria-current="page">المقالات</li>
        </ol>
      </div>
    </nav>
    <section class="properties properties-page">
      <div class="container">
        <div class="property-content">
          <form @submit="onSubmitSearchForm">
            <div class="row">
              <div class="col-12">
                <div class="input-area" >
                  <label class="form-label" for="search_text"> </label>
                  <input
                    class="form-control"
                    v-model="formDataFields.text"
                    placeholder="البحث"
                    id="search_text"
                    type="text"
                  />
                </div>
              </div>
              <div class="col-12">
              </div>
            </div>
          </form>
             <br>
              <br>
              <br>
          <div class="row">
            <div class="col-md-4 col-lg-3">
              <div class="filter-properties">
                <div class="type" v-if="blogsData.categories">
                  <div class="subtitle">
                    <h3>اقسام المدوانات</h3>
                  </div>
                  <div
                    class="form-check"
                    v-for="category in blogsData.categories"
                    :key="category.id"
                  >
                    <input
                      @change="onSubmitForm"
                      class="form-check-input"
                      :id="'propertyTypeRadio' + category.id"
                      v-model="formDataFields.category_id"
                      :value="category.id"
                      type="radio"
                      name="propertyTypeRadio"
                    />
                    <label
                      class="form-check-label"
                      :for="'propertyTypeRadio' + category.id"
                    >
                      {{ category.title }}</label
                    >
                  </div>
                </div>
                <hr />
              </div>
            </div>
            <div class="col-md-8 col-lg-9" v-if="filterVal.length">
              <div class="row">
                <div
                  class="col-md-12 col-lg-4"
                  v-for="property in filterVal"
                  :key="property.id"
                  data-aos="fade-left"
                  data-aos-anchor="#example-anchor"
                  data-aos-offset="500"
                  data-aos-duration="500"
                >
                  <div class="property-box">
                    <div class="property-image">
                      <img :src="property.image" :alt="property.title" />
                    </div>
                    <div class="property-details">
                      <router-link :to="`/blog/${property.id}`">
                        <h2 class="title">
                          {{ property.title }}
                        </h2>
                      </router-link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-8 col-lg-9" v-else>
              <div class="row">
                <div
                  class="col-md-12 col-lg-4"
                  data-aos="fade-left"
                  data-aos-anchor="#example-anchor"
                  :data-aos-offset="property.id * 100"
                  :data-aos-duration="property.id * 50"
                  v-for="property in blogsData.data"
                  :key="property.id"
                >
                  <div class="property-box">
                    <div class="property-image">
                      <img :src="property.image" :alt="property.title" />
                    </div>
                    <div class="property-details">
                      <router-link :to="`/blog/${property.id}`">
                        <h2 class="title">
                          {{ property.title }}
                        </h2>
                      </router-link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapState, mapActions } from "vuex";

export default {
  name: "BlogsView",
  data: function () {
    return {
      formDataFields: {
        category_id: null,
      },
      filterVal: [],
    };
  },
  methods: {
    ...mapActions(["filterFormData"]),
    onSubmitSearchForm(e) {
      e.preventDefault();

      this.filterFormData(this.formDataFields).then(() => {
        if (this.$store.state.searchParams) {
          e.target.reset();
          this.formDataFields.text = null;
        }
      });
    },


    onSubmitForm(e) {
      e.preventDefault();
      this.filterVal = this.blogsData.data.filter(
        (prop) => prop.category_id == e.target.value
      );
    },
  },

  computed: {
    ...mapState(["blogsData"]),
  },
  created() {
    this.$store.dispatch("getBlogData");
  },
};
</script>